@import "../../global.scss";

.topbar {
  width: 100%;
  height: 70px;
  background-color: #495057;
  color: $mainColor;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 1s ease;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      padding: 10px 0 0 5px;
      width: 90%;
      overflow: visible;
    }

    .left {
      display: flex;
      align-items: center;

      @include mobile {
        height: 50px;
        width: 500px;
        padding-right: 10px;
        margin: 0 0 0 0;
      }

      .logo {
        font-size: 40px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;
      }

      #logo {
        font-size: 40px;
        font-weight: 700;
        text-decoration: none;
        color: black;
        margin-right: 40px;

        @include mobile {
          font-size: 20px;
          font-weight: 400;
          // padding-right: 30px;
          display: none;
          margin-right: none;
        }
      }

      .itemContainer {
        display: flex;
        align-items: center;
        margin-left: 30px;

        .icon {
          font-size: 35px;
          margin-right: 5px;

          @include mobile {
            font-size: 200%;
            margin-right: 0;
          }
        }

        span {
          font-size: 15px;
          font-weight: 400;
          color: #d3d3d3;
          margin: 0 35px 0 0;

          @include mobile {
            font-size: 10px;
            margin-right: 5px;
            // width: 80%;
          }
        }
      }
    }

    .right {
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span {
          width: 100%;
          height: 3px;
          background-color: $mainColor;
          transform-origin: left;
          transition: all 2s ease;
        }
      }
    }
  }

  &.active {
    background-color: $mainColor;
    color: white;

    .hamburger {
      span {
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
